#hero {
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;

    height: 70vh;
    color: #ffffff;
    text-align: center;
}

#hero video, #hero img {
    object-fit: cover;
    position: absolute;

    mask: radial-gradient(ellipse 100% 100% at top, #ffffff, transparent 100%);
}

#hero #overlay {
    z-index: 2;
    display: grid;
    grid-template-columns: 2fr 1fr;
    justify-content: center;
    align-items: center;
    text-align: left;

    gap: 1rem;

    text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);    
}

#hero #overlay img {
    position: relative;
    object-fit: cover;
    
    width: 100%;
    aspect-ratio: 1/1;

    border-radius: 100%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    mask: none;
}

#hero #overlay h1 {
    font-size: 3rem;
    font-weight: 700;
}

#hero #overlay h2 {
    font-size: 1.25rem;
    font-weight: 400;
}

#hero #overlay p {
    padding-top: 1rem;
}